<template>
    <div class="">
    
    
    
        <!-- heading -->
        <v-flex xs12 sm10 md8 offset-xs0 offset-sm1 offset-md2>
            <v-card>
                <v-card-text class="text-xs-center ma-1">Cows</v-card-text>
            </v-card>
        </v-flex>
        <!-- heading completed -->
<!-- need search box  -->
        <!-- Cows -->
        <v-container>
            <v-layout row wrap>
                <v-flex xs12 sm10 md8 offset-xs0 offset-sm1 offset-md2>
                    <v-expansion-panel class="elevation-1 ma-1 pa-1">
                        <v-expansion-panel-content class="elevation-1 ma-1 pa-1" v-for="cow in cows" v-bind:key='cow.id'>
                            <div slot="header">
                                 <router-link v-bind:to="{ name: 'ViewCow', params: { name: cow.name }}">
    
                                    <v-btn flat>{{cow.name}}</v-btn>
                                </router-link>
                            </div>
                            <v-card>
                                <v-card-text>
                                    <li>color:{{cow.color}}</li>
                                </v-card-text>
    
                               
    
                            </v-card>
    
                        </v-expansion-panel-content>
                         <router-link v-bind:to="{ name: 'NewCow'}">
     <v-btn
              
              dark
              fab
              small
              bottom
              top
              color="grey"
              class="elevation-1 ma-0 pa-0"
            >
              <v-icon>add</v-icon>
            </v-btn>
                                    
                                </router-link>
                          
                    </v-expansion-panel>
                </v-flex>
                            
            </v-layout>

    
        </v-container>

    </div>
</template>

<script>
    import db from './firebaseInit'
    
    export default {
        name: 'Dashboard',
        data() {
            return {
                cows: []
    
            }
        },
        created() {
            db.collection('cows').get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    const data = {
                        'id': doc.id,
                        'name': doc.data().name,
                        'color':doc.data().color,
                        'knowdob':doc.data().knowdob,
                        'dow':doc.data().dow
                    }
                    this.cows.push(data)
                });
            })
    
        }
    }
</script>



